import React, { useState, useEffect } from 'react';
import data from './data/Land.json'; // 

const LandList = ({ onSelectFeature }) => {
    const [searchTerm, setSearchTerm] = useState('');
    const [filteredData, setFilteredData] = useState([]);

    useEffect(() => {
        if (searchTerm.trim() === '') {
            setFilteredData([]); //  
            return;
        }

        //  ""
        const results = data.features.filter((feature) =>
            feature.properties[""]?.toString().includes(searchTerm)
        );
        setFilteredData(results);
    }, [searchTerm]);

    const handleSearchChange = (event) => {
        setSearchTerm(event.target.value);
    };

    const handleSelect = (feature) => {
        onSelectFeature(feature); //  MapContents
        setSearchTerm(''); // 
        setFilteredData([]); // 
    };

    return (
        <div style={{
            width: '100%', 
            height: '100vh', 
            padding: '10px', 
            background: '#f9f9f9',
            borderRight: '1px solid #ddd',
            boxSizing: 'border-box'
        }}>
            <h3></h3>
            <input
                type="text"
                placeholder="..."
                value={searchTerm}
                onChange={handleSearchChange}
                style={{
                    width: '95%',
                    padding: '8px',
                    marginBottom: '10px',
                    borderRadius: '5px',
                    border: '1px solid #ccc'
                }}
            />
            {filteredData.length > 0 && (
                <ul style={{
                    listStyle: 'none',
                    padding: '5px',
                    maxHeight: '400px',
                    overflowY: 'auto',
                    border: '1px solid #ccc',
                    borderRadius: '5px',
                    background: '#fff'
                }}>
                    {filteredData.map((feature, index) => (
                        <li key={index}
                            onClick={() => handleSelect(feature)}
                            style={{
                                padding: '8px',
                                cursor: 'pointer',
                                borderBottom: '1px solid #ddd'
                            }}>
                            {feature.properties[""]} 
                            ( {feature.properties[""]})
                        </li>
                    ))}
                </ul>
            )}
        </div>
    );
};

export default LandList;
